html[data-theme='light'] {
    --bg: #ffffff;
    --background: #fafafa;
    --color-text: #333333;
    --textLight: #6c6c6c;
    --primary: #00a6a4;
    --primaryDisable: #00a6a455;
    --brandSecondary: #294c88;
    --yellow: #f3b92a;
    --divider: #e5e5e5;
    --placeholder: #ABABAB;
    --menu: #fafafa;
    --cardColor: #fff;
    --green: #00a6a4;
    --red: #EA2F2F;
    --white: #fff;
    --slideCard: #f2fbfb;
    --icon-color: #999999;
    --greyLight: #f1f1f1;
    --primaryLight: #e2f1f1;
    --primaryLite: #E7F5F5;
}

html[data-theme='dark'] {
  --bg: #001516;
  --background: #fafafa;
  --color-text: #efffff;
  --textLight: #cccccc;
  --primary: #13abb1;
  --primaryDisable: #13abb155;
  --brandSecondary: #7f93d3;
  --divider: #666b6b;
  --placeholder: #cccccc;
  --menu: #1f1f1f;
  --cardColor: #1d1d1d;
  --green: #00ebb1;
  --red: #f26b55;
  --slideCard: #1d1d1d;
  --icon-color: #999999;
  --greyLight: #f1f1f1;
}

