/* ----------------------Floating-Label---------------------------*/


/****  floating-Lable style start ****/

.floating-form {
  margin-top: 10px;
  background: var(--bg);
  font-size: var(--font-normal);
}

.floating-label {
  display: flex;
  flex-flow: column-reverse;
  margin-bottom: 15px;
  background: var(--bg);
  font-size: var(--font-normal);
}


/* .floating-label-dob {
  display: flex;
  flex-flow: column-reverse;
  background: var(--bg);
}

/**
* Add a transition to the label and input.
* I'm not even sure that touch-action: manipulation works on
* inputs, but hey, it's new and cool and could remove the 
* pesky delay.
*/

.floating-label-input,
.floating-input {
  transition: all 0.2s;
  touch-action: manipulation;
  background: var(--bg);
  font-size: var(--font-normal);
}

.floating-input {
  font-size: var(--font-normal);
  border: 0;
  height: 30px;
  border-bottom: 1px solid var(--divider);
  -webkit-appearance: none;
  border-radius: 0;
  padding: 0;
  cursor: pointer;
  color: var(--color-text);
  background: var(--bg);
}

.floating-input:focus {
  outline: 0;
  border-bottom: 1px solid #00a6a4;
  caret-color: var(--primary);
  background: var(--bg);
  font-size: var(--font-normal);
}

.floating-label-input {
  font-size: var(--font-normal);
  font-weight: normal;
  color: var(--placeholder);
  background: var(--bg);
}


/**
* Translate down and scale the label up to cover the placeholder,
* when following an input (with placeholder-shown support).
* Also make sure the label is only on one row, at max 2/3rds of the
* field—to make sure it scales properly and doesn't wrap.
*/

.floating-input:placeholder-shown+.floating-label-input {
  cursor: pointer;
  max-width: 66.66%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transform-origin: left bottom;
  font-size: var(--font-helper);
  transform: translate(0, 1.75rem);
  background: var(--bg);
}


/**
* By default, the placeholder should be transparent. Also, it should 
* inherit the transition.
*/

::-webkit-input-placeholder {
  opacity: 1;
  font-size: var(--font-normal);
  color: var(--placeholder);
  background: var(--bg);
}


/**
* Show the placeholder when the input is focused.
*/

.floating-input:focus::-webkit-input-placeholder {
  opacity: 1;
  background: var(--bg);
  font-size: var(--font-normal);
}


/**
* When the element is focused, remove the label transform.
* Also, do this when the placeholder is _not_ shown, i.e. when 
* there's something in the input at all.
*/

.floating-input:not(:placeholder-shown)+.floating-label-input,
.floating-input:focus+.floating-label-input {
  transform: translate(0, 0) scale(1);
  cursor: pointer;
  color: var(--primary);
  background: var(--bg);
  font-size: var(--font-normal);
}


/****  floating-Lable style end ****/


