/*--------------input-btn--------------*/

.btn-input {
  border: 2px solid var(--divider) !important;
  border-radius: 10px !important;
  align-items: center;
  width: 90%;
  margin-left: 20px;
}

.underLineBlue {
  color: var(--brandSecondary);
  border-bottom: 1px dashed var(--brandSecondary);
}


/* -----------------bottom drawer---------------------- */

.bottom-drawer .modal-dialog {
  width: 100%;
  margin: 0;
  position: absolute;
  bottom: -100px;
  transition: transform 0.4s !important;
}

.modal.fade.show .modal-dialog {
  transform: translate(0, -100px) !important;
}

.bottom-drawer .modal-footer {
  display: flex;
  margin: 0;
  gap: 10px;
  padding: 4%;
  border-top: 1px solid #e5e5e5;
}

.bottom-drawer .modal-footer button {
  flex: 1;
  height: 44px;
  border: 1px solid #00a6a4;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}

.bottom-drawer .modal-footer button.close {
  background: #fff;
  color: #00a6a4;
}

.bottom-drawer .modal-footer button.confirm {
  background: #00a6a4;
  color: #fff;
}

.bottom-drawer input[type="radio"] {
  margin-right: 10px;
}

label.radio {
  display: block;
  margin-bottom: 6%;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}

label.radio input[type="radio"] {
  margin-right: 4%;
}

.normalInput {
    outline: none;
    border: none;
    border-bottom: 1px solid #e5e5e5;
    margin-top: 2%;
    margin-bottom: 4%;
    font-size: 24px;
    font-weight: 500;
    width: 100%;
    color:#294C88;
}

.normalInput::placeholder {
  font-size: 24px;
  font-weight: 500;
  color: #ABABAB;
}

.normalInput.rupee {
    background-image: url('/src/assets/SVG/rupee-symbol.svg');
    padding-left: 20px;
    background-size: 16px;
    background-repeat: no-repeat;
    background-position-y: 9px;
}
.normalInput:disabled {
    color:#294C88;
    background-color: transparent
}
.footer-button {
  width: 100%;
  height: 44px;
  font-weight: 500;
  font-size: 14px;
  border-radius: 6px;
}

.hiddden-scrollbar {
  overflow-x: auto;
  white-space: nowrap;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.hiddden-scrollbar::-webkit-scrollbar {
  display: none;
  /*chrome and Safari */
}

.overflowText {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.outline-blue-button {
  background: #fff;
  border-radius: 6px;
  color: #294C88;
  border: 1px solid #294C88;
  height: 44px;
  width: 100%;
  font-size: 14px;
  font-weight: 500;
}




